.header {
  position: absolute;
  top: 0;
  left: 0;
  padding: 40px 0;
  width: 100%;

  &__right {
    @include flex-v-center;

    @include small-tablet {
      margin-top: 30px;
      padding-bottom: 10px;
      width: 100%;
      overflow-x: auto;
      overflow-y: hidden;

    }
  }

  &__container {
    @include flex-all-sb;

    @include small-tablet {
      flex-direction: column;
    }
  }
}

.header__search {
  margin-right: 35px;
  width: 24px;
  height: 24px;
  color: var(--light);
  transition: color 0.2s ease-in-out;
}

.header__search svg {
  display: block;
  width: 100%;
  height: 100%;
  stroke: currentColor;
  object-fit: cover;
}

.header__search:hover {
  color: var(--grey);
}

.logo {
  font-weight: 500;
  font-size: 32px;
  line-height: 130%;
  color: var(--light);
}
