.btn {
  padding: 20px 62px 19px 62px;
  min-width: 180px;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
  text-align: center;
  color: var(--dark);
  background-color: var(--light);
}

.btn--light {
  color: var(--dark);
  background-color: var(--light);
}

.btn--dark {
  color: var(--light);
  background-color: var(--dark);
}


