h1 {
  font-weight: 600;
  font-size: 96px;
  line-height: 116px;
  color: var(--dark);

  @include small-tablet {
    font-size: 70px;
    line-height: 84px;
  }

  @include mobile {
    font-size: 40px;
    line-height: 54px;
  }
}

h2 {
  font-weight: 500;
  font-size: 32px;
  line-height: 130%;
  color: var(--dark);
}

h2.title-bold {
  font-weight: 700;
  font-size: 44px;
  line-height: 140%;
  color: var(--dark);

  @include mobile {
    font-size: 30px;
    line-height: 44px;
  }
}

h3 {
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
  color: var(--grey);
}

p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--dark);
}
