.about {
  &__top {
    padding-top: var(--offset);
    padding-bottom: var(--offset);
  }

  &__item:not(:last-child) {
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
  }

  &__descr {
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}

.about__video {
  position: relative;
  width: 100%;
  height: 630px;
  overflow: hidden;
}

.about_video video {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about__play {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100px;
  height: 100px;
  transform: translate(-50%, -50%);
  color: var(--light);
  transition: color 0.2s ease-in-out;

  svg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  circle {
    fill: currentColor;
  }

  &:hover {
    color: var(--grey);
  }
}

.about__play--hidden {
  display: none;
}

.completed {
  &__value {
    font-weight: 300;
    font-size: 64px;
    line-height: 140%;
    color: var(--dark);
  }

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--grey);
  }
}
