.nav {
  &__list {
    @include flex-v-center;
  }

  &__item:not(:last-child) {
    margin-right: 35px;
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--light);
    transition: color 0.2s ease-in-out;

    &:hover {
      color: var(--grey);
    }
  }
}
