.portfolio {
  padding-top: var(--offset);
  padding-bottom: var(--offset);

  &-item:not(:last-child) {
    margin-bottom: 120px;

    @include small-tablet {
      margin-bottom: 60px;
    }
  }

  &-item__col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  &-item--reverse .portfolio-item__col:first-child {
    order: 2;

    @include small-tablet {
      order: 1;
    }
  }

  &-item--reverse .portfolio-item__col:last-child {
    order: 1;

    @include small-tablet {
      order: 2;
    }
  }

  &-item__title {
    margin: 0;
    margin-bottom: 20px;
  }

  &-item__desc {
    margin: 0;
    margin-bottom: 60px;
  }
}


