.hero {
  padding-top: 270px;
  height: 100vh;
  min-height: 900px;
  background-image: url('../img/hero-bg.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  left: 0;
  top: 0;

  @include mobile {
    height: auto;
    min-height: auto;
    padding-top: 200px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    max-width: 930px;
    color: var(--light);

    @include small-tablet {
      font-size: 70px;
      line-height: 84px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 50px;
    max-width: 468px;
    color: var(--light);
  }
}

