.quote {
  padding-bottom: var(--offset);

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--grey);
  }

  &__content {
    margin: 0;
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark);
    max-width: 820px;

    @include mobile {
      font-size: 26px;
      line-height: 40px;
    }
  }

  &__autor {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark);

    span {
      color: var(--grey);
    }
  }
}
