.footer {
  padding-top: 90px;
  padding-bottom: 150px;
  background-color: var(--dark);

  @include small-tablet {
    padding-bottom: 90px;
  }

  &__address {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--grey);
  }

  &__logo {
    display: inline-block;
    margin-bottom: 48px;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 32px;
    line-height: 130%;
    color: var(--light);
  }

  &__form {
    position: relative;
    width: 100%;
  }

  &__input {
    border: none;
    border-bottom: 1px solid var(--grey);
    padding: 24px 0;
    padding-right: 30px;
    width: 100%;
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: var(--light);
    background-color: transparent;

    &::placeholder {
      color: var(--grey);
    }
  }

  &__btn {
    position: absolute;
    bottom: 24px;
    right: 0;
    width: 24px;
    height: 24px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}



.social {
  display: flex;
  align-items: center;

  &__item:not(:last-child) {
    margin-right: 20px;
  }

  &__link {
    display: block;
    width: 40px;
    height: 40px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

