.services {
  margin-bottom: var(--offset);

  &__image {
    width: 100%;
    height: 900px;
    margin-bottom: var(--offset);
    object-fit: cover;
  }

  &-item {
    padding: 40px 0;
    border-top: 1px solid rgba(29, 29, 31, 0.4);

    &__title {
      margin: 0;
      font-weight: 500;
      font-size: 32px;
      line-height: 130%;
      color: var(--dark);
    }

    &__descr {
      margin: 0;
      color: var(--grey);
    }
  }
}
